<template>
  <!-- 部门管理 -->

  <div>
    <div v-if="$route.name == 'department_manage'">
      <mrc_ess_skLoaders :skLoading='skLoading'>
        <v-card>
          <v-card-text>
            <parent-company v-for="item in companyList"
                            :key='item.uuid'
                            :companyObj='item' />
          </v-card-text>
        </v-card>
      </mrc_ess_skLoaders>
    </div>
    <router-view v-else></router-view>
  </div>

</template>

<script>
import { get_company_list } from '@/service/api/company_set'
import parentCompany from './components/parentCompany'
export default {
  components: { parentCompany },
  props: {},
  data () {
    return {
      companyList: [],
      skLoading: false
    };
  },
  created () { },
  async mounted () {
    if (this.$route.name == 'department_manage') {
      await this.getList()
      this.skLoading = false
    }
  },
  methods: {
    // 获取公司列表
    async getList () {
      this.skLoading = true
      const list = await get_company_list({
        client_uuid: this.$store.state.currentCompanyClientUuid
      })
      if (list.code == 200) {
        this.companyList = list.data
      }
      this.skLoading = false
    },
    // 跳转部门列表页
    // handleAdd() {
    //   this.$store.commit('set_subPage_name', '')
    //   this.$store.commit('set_uuid', '')
    //   this.$router.push({
    //     name: 'department_list'
    //   })
    // },
  },
  computed: {},
  watch: {
    async '$route' (val) {
      if (val.name == 'department_manage') {
        await this.getList()
        this.$store.commit('setTab', 0)
      }

    }
  },
};
</script>

